////
/// Apply predefined standar ratios to elements
/// @group Objects
/// @link https://github.com/inuitcss/inuitcss/
/// @link http://alistapart.com/article/creating-intrinsic-ratios-for-video
////

/// A list of aspect ratios that get generated as modifier classes.
/// @type Map - Default common rations
$o-ratios-list: (
  (2:1),
  (4:3),
  (16:9),
) !default;

.o-ratio {
  position: relative;
  display: block;
  overflow: hidden;

  &:before {
    content: "";
    display: block;
    width: 100%;
    padding-bottom: 100%; /* [1] */
  }
}


.o-ratio__content,
.o-ratio > iframe,
.o-ratio > embed,
.o-ratio > object {
  position: absolute;
  top:    0;
  bottom: 0;
  left:   0;
  height: 100%;
  width:  100%;
}



/* Ratio variants.
========================================================================== */

/**
* Generate a series of ratio classes to be used like so:
*
*   <div class="o-ratio  o-ratio--16:9">
*
*/

@each $ratio in $o-ratios-list {

   @each $antecedent, $consequent in $ratio {

      @if (type-of($antecedent) != number) {
         @error "`#{$antecedent}` needs to be a number."
      }

      @if (type-of($consequent) != number) {
         @error "`#{$consequent}` needs to be a number."
      }

      .o-ratio--#{$antecedent}\:#{$consequent}:before {
         padding-bottom: ($consequent/$antecedent) * 100%;
      }

   }

}
